
.my-qr-code {
    height: 100vh;
}

.header-timer {
    text-align: center;
    color: #fff;
    font-size: 20px;
    font-weight: 500;

    .date-detail {
        margin-right: 4px;
    }
}

.qrcode-center {
    text-align: center;
    margin-top: 18px;

    .person-name {
        font-size: 20px;
        font-weight: 500;
        color: #fff;
    }

    .qrcode-img {
        width: 158px;
        height: 178px;
        background-color: #fff;
        margin: 8px auto;
        padding: 4px;

        .code-border {
            position: relative;
            height: 150px;
            background-image: -webkit-linear-gradient(90deg, #0cbaa3, lighten(#0cbaa3, 40%), #0cbaa3);
            padding: 4px;
            .code-center {
                position: absolute;
                left: 0;
                right: 0;
                top: 0;
                bottom: 0;
                margin:auto;
            }
        }
    }
}
