
.mobile-timer {
    color: #fff;
    font-size: 12px;

    &::after {
        display: block;
        content: "";
        clear: both;
    }

    .left-timer {
        float: left;
        display: flex;
        align-items: center;
    }

    .right-network {
        float: right;
        display: flex;
        align-items: center;

        .hd-text {
            padding: 2px 2.4px;
            background-color: #fff;
            border-radius: 4px;
            color: #0808088a;
            font-family: cursive;
            font-size: 12px;
            line-height: 10px;
            transform: scale(0.6);
        }

        .singnal-wrapper {
            position: relative;
            margin: 0 4px;

            .singnal-upper-text {
                position: absolute;
                left: -8px;
                top: -6px;
                transform: scale(0.5);
            }
        }
    }

    .power-wrapper {
        position: relative;
        margin-top: 1.4px;

        .power-count {
            position: absolute;
            left: 0.5px;
            right: 0px;
            top: -1px;
            bottom: 0;
            margin: auto;
            font-size: 12px;
            transform: scale(0.54);
        }
    }

    .small-img {
        width: 14px;
        height: 14px;
    }

    .mid-img {
        width: 16px;
        height: 16px;
    }

    .long-img {
        width: 22px;
        height: 17px;
        transform: scale(1.3);
    }
}
